import React, { useState, useRef, useEffect, useCallback } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import ChatBackground from './ChatBackground';
import { 
    sendChatMessage, 
    getLocalChatSession, 
    addMessageToLocalSession,
    createLocalChatSession,
    setCurrentSessionId
} from '../utils/chatApi';
import './ChatPage.css';

const NOTICE_API_URL = process.env.REACT_APP_NOTICE_API_URL;

function ChatPage() {
    const { chatId } = useParams();
    const navigate = useNavigate();
    const location = useLocation();
    const [messages, setMessages] = useState([]);
    const [inputMessage, setInputMessage] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [sessionId, setSessionId] = useState(chatId || null);
    const messagesEndRef = useRef(null);
    const autoQuestionSent = useRef(false);
    const messagesRef = useRef([]);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    };

    useEffect(() => {
        scrollToBottom();
    }, [messages]);

    // messages   ref 
    useEffect(() => {
        messagesRef.current = messages;
    }, [messages]);

    //     
    useEffect(() => {
        const loadSessionMessages = async () => {
            if (!sessionId) {
                //    
                fetchNoticeOnly();
                return;
            }

            //   ID 
            setCurrentSessionId(sessionId);

            //   
            const session = getLocalChatSession(sessionId);
            
            if (session && session.messages && session.messages.length > 0) {
                //      
                const formattedMessages = session.messages.map(msg => ({
                    role: msg.role === 'user' ? 'user' : 'ai',
                    content: msg.content,
                    timestamp: new Date(msg.timestamp)
                }));
                setMessages(formattedMessages);
            } else {
                //    
                fetchNoticeOnly();
            }
        };

        loadSessionMessages();
    }, [sessionId]);

    const fetchNoticeOnly = async () => {
        try {
            const res = await fetch(NOTICE_API_URL);
            const data = await res.json();

            if (data.status === "success" && data.notice) {
                const { title, link } = data.notice;
                setMessages([
                    {
                        role: "ai",
                        content: (
                            <div>
                                <div>!  .</div>
                                <div style={{ marginTop: "10px" }}>
                                    <strong>  [{title}]</strong>
                                </div>
                                <a href={link} target="_blank" rel="noopener noreferrer" className="notice-link">
                                     
                                </a>
                            </div>
                        ),
                        timestamp: new Date(),
                    },
                ]);
            } else {
                setMessages([
                    {
                        role: "ai",
                        content: (
                            <div>
                                !  .<br />
                                     .
                            </div>
                        ),
                        timestamp: new Date(),
                    },
                ]);
            }
        } catch (error) {
            console.error("  :", error);
            setMessages([
                {
                    role: "ai",
                    content: (
                        <div>
                            !  .<br />
                            (   .)
                        </div>
                    ),
                    timestamp: new Date(),
                },
            ]);
        }
    };

    const sendMessage = useCallback(async (messageToSend = null) => {
        const message = messageToSend || inputMessage.trim();
        if (!message || isLoading) return;

        const userMessage = {
            role: 'user',
            content: message,
            timestamp: new Date()
        };

        //   
        setMessages(prev => [...prev, userMessage]);
        setInputMessage('');
        setIsLoading(true);

        //    
        let currentSessionId = sessionId;
        if (!currentSessionId) {
            const newSession = createLocalChatSession();
            currentSessionId = newSession.id;
            setSessionId(currentSessionId);
            setCurrentSessionId(currentSessionId);
            // URL 
            navigate(`/chat/${currentSessionId}`, { replace: true });
        }

        //    
        addMessageToLocalSession(currentSessionId, {
            role: 'user',
            content: message,
            timestamp: new Date().toISOString()
        });

        //     (ref )
        const currentMessages = [...messagesRef.current, userMessage];
        
        //    (  )
        const chatHistory = currentMessages.map(msg => ({
            role: msg.role === 'user' ? 'user' : 'assistant',
            content: typeof msg.content === "string" ? msg.content : ""
        }));

        // API  ( )
        try {
            const data = await sendChatMessage(message, chatHistory);

            if (data.success) {
                const aiMessage = {
                    role: 'ai',
                    content: data.response,
                    timestamp: new Date()
                };
                setMessages(prev => [...prev, aiMessage]);

                //  AI  
                addMessageToLocalSession(currentSessionId, {
                    role: 'assistant',
                    content: data.response,
                    timestamp: new Date().toISOString()
                });
            } else {
                const errorMessage = {
                    role: 'ai',
                    content: `: ${data.response}`,
                    timestamp: new Date()
                };
                setMessages(prev => [...prev, errorMessage]);
            }
        } catch (error) {
            console.error(' :', error);
            const errorMessage = {
                role: 'ai',
                content: '.    .    .',
                timestamp: new Date()
            };
            setMessages(prev => [...prev, errorMessage]);
        } finally {
            setIsLoading(false);
        }
    }, [inputMessage, isLoading, sessionId, navigate]);

    //   
    useEffect(() => {
        const autoQuestion = location.state?.autoQuestion;
        if (autoQuestion && !autoQuestionSent.current && messages.length > 0) {
            //      
            autoQuestionSent.current = true;
            setTimeout(() => {
                sendMessage(autoQuestion);
            }, 500); //     
        }
    }, [messages, location.state, sendMessage]);

    // chatId  sessionId 
    useEffect(() => {
        if (chatId) {
            setSessionId(chatId);
        } else {
            setSessionId(null);
        }
    }, [chatId]);

    const handleKeyDown = (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
            e.preventDefault();
            sendMessage();
        }
    };

    const clearChat = () => {
        autoQuestionSent.current = false;
        setMessages([
            {
                role: 'ai',
                content: ' .   !',
                timestamp: new Date()
            }
        ]);
    };

    return (
        <ChatBackground>
            <div className="chat-container">

                <div className="chat-header">
                    <div className="flex items-center space-x-3">
                        <button 
                            onClick={() => navigate('/')}
                            className="p-2 hover:bg-white/10 rounded-full transition-colors duration-200"
                        >
                            <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                            </svg>
                        </button>
                        <h1>MJC AI Chat</h1>
                    </div>
                    <button className="clear-btn" onClick={clearChat}>
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15" />
                        </svg>
                        <span></span>
                    </button>
                </div>

                <div className="messages-container">
                    {messages.map((message, index) => (
                        <div
                            key={index}
                            className={`message ${message.role === 'user' ? 'user-message' : 'ai-message'}`}
                        >
                            {message.role === 'ai' && (
                                <div className="ai-avatar">
                                    <img 
                                        src="/ 1.svg" 
                                        alt="MJC AI" 
                                        className="avatar-image"
                                        onError={(e) => {
                                            e.target.style.display = 'none';
                                            e.target.nextSibling.style.display = 'flex';
                                        }}
                                    />
                                    <div className="avatar-fallback" style={{display: 'none'}}>
                                        <span className="avatar-text">AI</span>
                                    </div>
                                </div>
                            )}

                            <div className="message-content">
                                <div className="message-text">
                                    {typeof message.content === "string" ? (
                                        <ReactMarkdown
                                            remarkPlugins={[remarkGfm]}
                                            components={{
                                                table: (props) => <table className="chat-table" {...props} />,}}>
                                             {message.content}
                                        </ReactMarkdown>
                                    ) : (
                                        message.content
                                    )}
                                </div>
                                <div className="message-time">
                                    {message.timestamp.toLocaleTimeString()}
                                </div>
                            </div>
                        </div>
                    ))}

                    {isLoading && (
                        <div className="message ai-message">
                            <div className="ai-avatar">
                                <img 
                                    src="/ 1.svg" 
                                    alt="MJC AI" 
                                    className="avatar-image"
                                    onError={(e) => {
                                        e.target.style.display = 'none';
                                        e.target.nextSibling.style.display = 'flex';
                                    }}
                                />
                                <div className="avatar-fallback" style={{display: 'none'}}>
                                    <span className="avatar-text">AI</span>
                                </div>
                            </div>
                            <div className="message-content">
                                <div className="typing-indicator">
                                    <div className="typing-dot"></div>
                                    <div className="typing-dot"></div>
                                    <div className="typing-dot"></div>
                                </div>
                            </div>
                        </div>
                    )}

                    <div ref={messagesEndRef} />
                </div>

                <div className="input-container">
                    <div className="input-wrapper">
                        <textarea
                            value={inputMessage}
                            onChange={(e) => setInputMessage(e.target.value)}
                            onKeyDown={handleKeyDown}
                            placeholder=" ... (Enter: , Shift+Enter: )"
                            className="message-input"
                            rows="2"
                            disabled={isLoading}
                        />
                        <button
                            onClick={() => sendMessage()}
                            disabled={!inputMessage.trim() || isLoading}
                            className="send-btn"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                            </svg>
                        </button>
                    </div>
                </div>
            </div>
        </ChatBackground>
    );
}

export default ChatPage;
